<template>
    <div class="income">
        <div class="income-ietm-1">
            <img src="../assets/income.png">
            <div>{{ $t("fov.title_6") }}</div>
        </div>
        <div class="income-ietm-2">
            <img src="../assets/expand.png">
            <div>{{ $t("fov.title_7") }}</div>
        </div>
        <div class="income-ietm-3">
            <div>{{ commission }}</div>
            <div>TRX</div>
        </div>
        <div class="income-ietm-4">{{ $t("fov.des_4") }}<b>&nbsp;{{ income }}&nbsp;</b>TRX</div>
        <div class="income-ietm-5">
            <button @click="withdraw">{{ $t("fov.withdraw") }}</button>
        </div>
        <div class="income-ietm-6">{{ $t("fov.input") }}: <span>{{ joinedTrx }}</span>TRX</div>
        <div class="income-ietm-7">{{ $t("fov.output") }}: <span>{{ withdrawedTrx }}</span>TRX</div>
    </div>
</template>

<script>

import formatNum from "./../numcomp";
export default {
    name: 'Income',
    props: [],
    data() {
        return {
            commission: '',
            income: '',
            joinedTrx: '',
            withdrawedTrx: ''
        }
    },

    created() {
      this.init();
    },

    methods: {

        fn(num) {
            return formatNum(num, 2)
        },

        fnd(num, d) {
            return formatNum(num, d)
        },

      async init() {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          this.account = window.tronWeb.defaultAddress.base58;
          this.tronWeb = window.tronWeb;

          this.initContract();
        } else {
          setTimeout(() => {
            this.init()
          }, 1000);
        }
      },

        async initContract() {
          this.fov = await this.tronWeb.contract().at(window.FOV_ADDR);
          this.info()
        },


        withdraw() {
            this.fov.withdraw().send({
              feeLimit: 100000000
            }).then(
                () => {
                    this.info()
                }
            )
        },

        info() {
            this.fov.myOrder(this.account).call().then(
                (r) => {
                  this.commission = this.tronWeb.fromSun(r.dEarn)
                  
                  this.joinedTrx = this.tronWeb.fromSun(r.joined)
                  this.withdrawedTrx =  this.fnd(this.tronWeb.fromSun(r.wded), 2);
                  this.sEarn = this.tronWeb.fromSun(r.sEarn)
                  this.income = this.fnd(parseFloat(this.sEarn) + parseFloat(this.commission), 2)
                  setTimeout(() => {
                    this.info()
                  }, 1500);

                }
            )
        },
    }
}

</script>


