            <div :class="active_token == 'TRX' ? 'active' : ''" @click="switchMine('TRX')">TRX{{ $t("fov.mine") }}</div>
            <div :class="active_token == 'EOS' ? 'active' : ''" @click="switchMine('TRX')">EOS{{ $t("fov.mine") }}</div>
