<script>
export default {
    name: 'Home',
    props: ['totalJoined'],
    data() {
        return {
            url: '',
            active_token: 'TRX'
        }
    },

    created() {
        var lang = localStorage.getItem('langs') || 'zh_CN'
        if(lang == 'zh_CN') {
            this.url = require('../assets/home.png')
        } else {
            this.url = require('../assets/homeEn.png')
        }
    },

    methods: {
      async init() {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          this.account = window.tronWeb.defaultAddress.base58;
          this.tronWeb = window.tronWeb;

          this.initContract();
        } else {
          setTimeout(() => {
            this.init()
          }, 1000);
        }
      },

        async initContract() {
          this.fov = await this.tronWeb.contract().at(window.FOV_ADDR);

        },

        langToggle() {
            if(this.$i18n.locale == 'zh_CN'){
                this.$i18n.locale = 'en_US';
                localStorage.setItem('langs','en_US');
                this.url = require('../assets/homeEn.png')
            }else {
                this.$i18n.locale = 'zh_CN';
                localStorage.setItem('langs','zh_CN');
                this.url = require('../assets/home.png')
            }
        },

        switchMine(token) {
            this.active_token = token
        }
    }
}

</script>


