<template>
  <div class="mine">
    <div class="mineStatus">
      <div class="over" v-if="mineStatus == 0">{{ $t("fov.over") }}</div>
      <div :class="mineLevel" v-if="mineStatus == 1">{{ $t("fov.income") }}</div>
    </div>
    <div class="mineLevel">
      <div>
        <div>
          <img :src="imgUrl">
          <div v-if="level == 4" class="levelText" :class="mineLevel">{{ $t("fov.level_4") }}</div>
          <div v-if="level == 3" class="levelText" :class="mineLevel">{{ $t("fov.level_3") }}</div>
          <div v-if="level == 2" class="levelText" :class="mineLevel">{{ $t("fov.level_2") }}</div>
          <div v-if="level == 1" class="levelText" :class="mineLevel">{{ $t("fov.level_1") }}</div>
        </div>

        <div>{{ $t("fov.daily_ratio") }}</div>
        <div :class="mineLevel">{{ mineData.rateReturn }}%</div>
      </div>
    </div>
    
    <div class="mineDetails">
      <div>
        <div>{{ $t("fov.settlement") }}: {{ $t("fov.content_1") }}</div>
        <div v-if="level == 4">{{ $t("fov.settlement") }}: {{ $t("fov.permanent") }}</div>
        <div v-else>{{ $t("fov.mine_day") }}: {{ mineData.day }}{{ $t("fov.day") }}</div>
        <div v-if="level == 4">{{ $t("fov.total_ratio") }}: ∞</div>
        <div v-else>{{ $t("fov.total_ratio") }}: {{ mineData.totalReturn }}%</div>
        <div>{{ $t("fov.join_standard") }}: {{ mineData.joinStandard }}TRX{{ $t("fov.minimum") }}</div>
        <div>{{ $t("fov.des_2") }}</div>
        <div class="number">
          <input v-if="level < 4" type="number" min="1030" v-model="inAmount">
          <input v-if="level == 4" type="number" min="50030" v-model="inAmount">
          <span style="margin-bottom: -0px; margin-left: 5px;">TRX</span>
        </div>
        <div style="font-size: 12px">{{ $t("fov.des_3") }}</div>
      </div>
    </div>

    <div class="join">
      <button @click="join()" :class="mineLevel">{{ $t("fov.join_now") }}</button>
    </div>
  </div>
</template>

<script>
import {
    Message,
    MessageBox
} from 'element-ui';

export default {
  name: 'Mine',
  props: ['level','status'],

  watch: {
    status() {
      this.mineStatus = this.status
    }
  },

  data() {
      return {
        mineLevel:'',
        mineStatus: '',
        imgUrl: '',
        inAmount: null,
        mineData: {
          rateReturn: '',
          day: '',
          totalReturn: '',
          joinStandard: '',
        },
        mineLevel1: {
          rateReturn: '6.9',
          day: 15,
          totalReturn: 103.5,
          joinStandard: 1030,
        },
        mineLevel2: {
          rateReturn: '3.8',
          day: 30,
          totalReturn: 114,
          joinStandard: 1030,
        },
        mineLevel3: {
          rateReturn: '2.9',
          day: 45,
          totalReturn: 130.5,
          joinStandard: 1030,
        },
        mineLevel4: {
          rateReturn: '1.0',
          day: '永久',
          totalReturn: '∞',
          joinStandard: 50030,
        },
      }
  },

  created() {
    this.mineStatus = this.status
    console.log("this.mineStatus:" + this.status)
    if(this.level == 1) {
      this.mineData = this.mineLevel1
      this.mineLevel = 'level-1'
      this.imgUrl = require('../assets/mineLevel1.png')
    } else if(this.level == 2) {
      this.mineData = this.mineLevel2
      this.mineLevel = 'level-2'
      this.imgUrl = require('../assets/mineLevel2.png')
    } else if(this.level == 3) {
      this.mineData = this.mineLevel3
      this.mineLevel = 'level-3'
      this.imgUrl = require('../assets/mineLevel3.png')
    }  else if(this.level == 4) {
      this.mineData = this.mineLevel4
      this.mineLevel = 'level-4'
      this.imgUrl = require('../assets/mineLevel4.png')
    }

    let addr = this.$route.params.p;
    console.log("p:" + addr + ", len:"  + addr.length)

    if (addr && addr.length === 34) {
        this.parentAddr = addr;
    } else {
        console.log("no parent");
    }

    this.init();
  },

  methods: {
    async init() {
      if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
        this.account = window.tronWeb.defaultAddress.base58;
        this.tronWeb = window.tronWeb;
        this.initContract();

      } else {
        setTimeout(() => {
          this.init()
        }, 1000);
      }
    },

    async initContract() {
      this.fov = await this.tronWeb.contract().at(window.FOV_ADDR);
    },

    join() {
      if (!this.parentAddr) {
          MessageBox.alert(i18n.t("fov.invalid_p"), i18n.t("fov.tip"), {
              confirmButtonText: i18n.t("fov.btn_ok")
          })
          return;
      } else {
        console.log("invest:" + this.inAmount)
          this.fov.join(this.parentAddr, this.level).send({
              callValue: this.tronWeb.toSun(this.inAmount),
              feeLimit: 100000000
          }).then(() => {
              this.mineStatus = 1;
              this.inAmount = null;
              this.$emit('join');
          })
      }

       

  },
  }
}

</script>


