<template>
<div v-loading.fullscreen="loading">
    <div class="moble">
        <Home :totalJoined = totalJoined></Home>
        <Mine 
            :level = 4
            :status = v4status
            v-on:join="join"
        ></Mine>
        <Mine 
            :level = 3
            :status = v3status
            v-on:join="join"
        ></Mine>
        <Mine 
            :level = 2
            :status = v2status
            v-on:join="join"
        ></Mine>
        <Mine 
            :level = 1
            :status = v1status
            v-on:join="join"
        ></Mine>
        <Income></Income>
        <Team></Team>
    </div>
</div>
</template>

<script>
/* eslint-disable */
import formatNum from "./../numcomp";
import Home from '@/components/Home.vue'
import Mine from '@/components/Mine.vue'
import Income from '@/components/Income.vue'
import Team from '@/components/Team.vue'


export default {
    name: 'Fov',
    components: {
        Home,
        Mine,
        Income,
        Team
    },
    data() {
      return {
        loading: null,
        v4status: 0,
        v3status: 0,
        v2status: 0,
        v1status: 0,
        totalJoined: null,
      }
    },

    created() {
        this.init();
    },

    methods: {
      async init() {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          this.account = window.tronWeb.defaultAddress.base58;
          this.tronWeb = window.tronWeb;
          console.log("inited account:" + this.account);

          this.initContract();

        } else {
          setTimeout(() => {
            this.init()
          }, 1000);
        }
      },

        fn(num) {
            return formatNum(num, 2)
        },

        fnd(num, d) {
            return formatNum(num, d)
        },

        async initContract() {
            this.fov = await await this.tronWeb.contract().at(window.FOV_ADDR);
            console.log("contract address:" + this.fov.address);
            this.loading = false;
            this.listOrder();
            this.getJoined();
        },

        langToggle() {
          if(this.$i18n.locale == 'zh_CN'){
            this.$i18n.locale = 'en_US';
            localStorage.setItem('langs','en_US');
          }else {
            this.$i18n.locale = 'zh_CN';
            localStorage.setItem('langs','zh_CN');
          }
        },

        getJoined(){
            this.fov.vaultsInfo().call().then(r => {
              this.totalJoined = this.tronWeb.fromSun(r[0])
            })  
        },

        async listOrder() {
          var timestamp = parseInt(Date.parse(new Date()) / 1000);
          console.log("timestamp:" + timestamp);
          let num = await this.fov.orderNum().call();
          for (let i = 0; i < num; i++) {
            this.fov.order(i).call().then(r => {
              let level = r[4];
              let endTs = r[3]

              console.log("endTs:" + endTs + ", level: " + level);
              
              if (level == 4) {
                this.v4status = 1;
              } else if (endTs > timestamp) {
                if(level == 1) {
                  this.v1status = 1;
                } else if (level == 2) {
                  this.v2status = 1;
                  console.log("r.v2status:" + this.v2status);
                } else if (level == 3) {
                  this.v3status = 1;
                } 
              }
            });
          }
        },

        join() {
          console.log("join");
          this.listOrder();
          this.getJoined();
        }
    }
}
</script>

