                </div>
            </div>

            <div>{{ $t("fov.view_push") }}</div>
        </div>
        <div class="team-item-2">{{ $t("fov.title_9") }}</div>
        <div class="team-item-3">{{ $t("fov.des_5") }}</div>
        <div class="team-item-4">{{ $t("fov.invitation_link") }}: <span class="copylinktext">{{uLink}}</span> 
        </div>
        <div class="team-item-5">
            <vue-qr :correctLevel="3" :autoColor="false" :text="uLink" :size="86" :margin="0" :logoMargin="3"></vue-qr>
        </div>
        <div>
            <div class="team-item-6">
                <div></div>
                <div>{{ $t("fov.des_6") }}</div>
            </div>
            <div class="team-item-6">
                <div></div>
                <div>{{ $t("fov.des_7") }}</div>
            </div>
            <div class="team-item-6">
                <div></div>
                <div>{{ $t("fov.des_8") }}</div>
            </div>
        </div>
    </div>
</template>

<script>
import VueQr from 'vue-qr'

import {
    Message
} from 'element-ui';

export default {
    name: 'Team',
    props: [],
    components: {
        VueQr
    },
    data() {
        return {
            copied: false,
            uLink: 'http://localhost:8080/#/i/',
            uLinkShort: ''
        }
    },

    created() {
      this.init()
    },

    methods: {
      async init() {
        if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
          this.account = window.tronWeb.defaultAddress.base58;
          this.tronWeb = window.tronWeb;

          this.uLink = process.env.VUE_APP_HOST + 'i/' + this.account

          this.uLinkShort = this.uLink;
        } else {
          setTimeout(() => {
            this.init()
          }, 1000);
        }
      },

      refresh() {
        // this.$router.go(this.uLink);
        location.replace(this.uLink)   
      },

      copy() {
          if (!navigator.clipboard) {
              var textArea = document.createElement("textarea");
              textArea.value = this.uLink;
              textArea.style.position = "fixed"; //avoid scrolling to bottom
              document.body.appendChild(textArea);
              textArea.focus();
              textArea.select();

              try {
                  var successful = document.execCommand('copy');
                  this.copied = true
                  var msg = successful ? 'successful' : 'unsuccessful';
                  console.log('Fallback: Copying text command was ' + msg);

                  Message.info({
                      "message":  i18n.t("fov.msg_link_copyed")
                  })

              } catch (err) {
              }

              document.body.removeChild(textArea);
          } else {
              navigator.clipboard.writeText(this.uLink).then(function () {
                  Message.info({
                      "message": i18n.t("fov.msg_link_copyed")
                  })
              }, function (err) {
                  Message.info({
                      "message": i18n.t("fov.msg_link_copyed_fail")
                  })
              });
          }

        }
    },
}

</script>


